/* Add CSS styles for waves background effect */
.bg-hero-image {
  position: relative;
  overflow: hidden;
  height: 100vh; /* Set the height to 100vh (viewport height) */
  background-image: url(images/Code\ typing-amico\ \(3\).png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
  
}

.bg-hero-image::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.8) 20%, rgba(0, 0, 0, 0) 100%);
  transform: translate(0, 100%);
  animation: waves 15s infinite linear;
  pointer-events: none; /* Add this line */
}


@keyframes waves {
  0% {
    transform: translate(0, -30%);
  }
  50% {
    transform: translate(0, -90%);
  }
  100% {
    transform: translate(0, -30%);
  }
}

@keyframes waves-reversed {
  0% {
    transform: translate(0, -50%);
  }
  100% {
    transform: translate(0, -20%);
  }
}
/* Add CSS styles for Projects and Contact sections */
.Project-root-container {
  position: relative;
  overflow: hidden;
  height: 100vh;
  background-image: url(images/Software\ engineer-amico\ \(1\).png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
}

.Project-root-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.8) 0%, rgba(0, 0, 0, 0) 100%);
  transform: translate(0, -55%);
  pointer-events: none;
  z-index: -1; /* Push the gradient below the content */
}

.Contact-text{
