.menu-overlay {
  position: fixed;
  top: 0;
  right: -100%;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 15;
  transition: right 0.4s ease-in-out;

}

.menu-open {
  right: 0;
}

.menu-overlay a {
  display: block;
  padding: 1rem;
  font-size: 1.5rem;

  text-align: center;
  transition: background-color 0.3s;
}

