/* Scrollbar Styles */
::-webkit-scrollbar {
  width: 8px; /* Width of the scrollbar */
}

/* Track */
::-webkit-scrollbar-track {
  background: #11181B; /* Color of the track */
}

/* Handle */
::-webkit-scrollbar-thumb {
  background-color:#44bae8;
  transition: 0.7s ease;
  border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background:#44bae8b0;
}